* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  font-weight: 800;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/Muller Regular.woff2") format("woff2"), url("../fonts/Muller Regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
}
body {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  background: #FFFFFF;
}

.header {
  padding: 14px 0;
  background: #FFFFFF;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.1);
}

.container {
  max-width: 1770px;
  margin: 0 auto;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__container .header__logo {
  width: 225px;
  height: 72px;
}
.header__container svg {
  width: 22px;
  height: 21px;
}

.hamburger-menu {
  display: none;
}

.header-list {
  display: flex;
  align-items: center;
}

.header-search__svg:hover,
.header-search__svg:hover path,
.header-search__svg:hover rect {
  stroke: #FF9900;
  transition: stroke 0.2s ease;
}

.header-search__svg:active,
.header-search__svg:active path,
.header-search__svg:active rect {
  stroke: #FF9900;
}

.header-list__item:not(:last-child) {
  margin-right: 85px;
}

.header-list__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  color: #000000;
}
.header-list__link:hover {
  padding-bottom: 7px;
  border-bottom: 2px solid #FF9900;
  transition: all 0.2s ease;
}
.header-list__link:focus {
  outline: 2px solid #FF9900;
}
.header-list__link:active {
  color: #E1670E;
  padding-bottom: 7px;
  border-bottom: 2px solid #E1670E;
  transition: all 0.2s ease;
}

.project {
  padding: 50px 0;
}

.project__container {
  max-width: 1770px;
  min-height: 620px;
  overflow: hidden;
}

.swiper {
  width: 1770px;
  border-radius: 32px;
  height: 620px;
  position: absolute !important;
}

.swiper-pagination-bullet {
  width: 15px !important;
  height: 15px !important;
  background-image: url("../img/project/swiper__bullet.svg") !important;
}

.swiper-pagination-bullet-active {
  background-image: url("../img/project/swiper_bullet_active.svg") !important;
}

