  left: 80px;
}

.project-box__title {
  font-weight: 800;
  font-size: 70px;
  line-height: 1.21;
  color: #FFFFFF;
  margin-bottom: 24px;
}

.project-box__subtitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
  color: #FFFFFF;
  margin-bottom: 90px;
}

.project-box__btn {
  padding: 25px 39px;
  font-weight: 700;
  font-size: 23px;
  line-height: 1.21;
  color: #FFFFFF;
  background: #FF9900;
  border-radius: 16px;
}
.project-box__btn:hover {
  background: #FFB84D;
  transition: all 0.2s ease;
}
.project-box__btn:focus {
  background: #FFB84D;
}
.project-box__btn:active {
  background: #E1670E;
}

.about {
