}
.box-right__list img {
  width: 190px;
  height: 165px;
  margin-right: 50px;
}

.box-right__desc {
  max-width: 450px;
}

.box-right__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.2;
  color: #000000;
  margin-bottom: 8px;
}

.box-right__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 2;
  color: #000000;
}

.work {
