}

.site-btn {
  background: #FF9900;
  border-radius: 16px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  color: #FFFFFF;
}
.site-btn:hover {
  background: #FFB84D;
  transition: all 0.2s ease;
}
.site-btn:focus {
  background: #FFB84D;
}
.site-btn:active {
  background: #E1670E;
}

.work-left__btn {
  padding: 20px 38px;
}

.btn-reset {
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
}

.work-left__btn:first-child {
  margin-right: 30px;
}

.work-left__btn--border {
  border: 1px solid #000000;
  border-radius: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: #000000;
}
.work-left__btn--border:hover {
  background: #666666;
  color: #ffffff;
  transition: all 0.2s ease;
}
.work-left__btn--border:focus {
  background: #666666;
  color: #ffffff;
}
.work-left__btn--border:active {
  background: #000000;
}

.work__btn:hover {
  color: #FF9900;
  transition: color 0.2s ease;
}

.work__btn:focus {
  outline: 2px solid #FF9900;
}

.work__btn:active {
  color: #E1670E;
}

.work-right {
  padding-top: 6px;
  width: 49%;
  height: 100%;
}

.work-right__img {
  width: 100%;
