}

.question {
  padding-bottom: 100px;
}

.question__header {
  font-weight: 400;
  font-size: 48px;
  line-height: 1.22;
  color: #000000;
  margin-bottom: 32px;
}

.ui-accordion-header-icon {
  display: none;
}

.ui-accordion .ui-accordion-header {
