  font-size: 24px;
  line-height: 1.33;
  color: #000000;
  padding: 39px 0;
  border-top: 1px solid #CACACA;
  margin: 0;
  background: #ffffff;
}
.ui-accordion .ui-accordion-header:hover {
  color: #FF9900;
  transition: color 0.2s ease;
}
.ui-accordion .ui-accordion-header:focus {
  color: #FF9900;
}
.ui-accordion .ui-accordion-header:active {
  color: #000000;
}

.accordion__subtitle {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.8;
  color: #000000;
  max-width: 1003px;
  overflow: hidden;
}

.ui-state-default {
  border: none;
}

.ui-widget-content {
  border: none;
  overflow: hidden;
}

.ui-icon-triangle-1-e {
  background-image: none;
}

.ui-state-hover .ui-icon {
  background-image: none;
}

.footer {
  background: #000000;
}

.footer__container {
  display: grid;
  grid-template-columns: 252px 570px 363px;
  align-items: center;
  justify-content: space-between;
  padding: 50px 75px;
}

.footer-left__logo {
  width: 224px;
  height: 72px;
  margin-bottom: 20px;
}

.footer-left__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 2;
  color: #CACACA;
}
.footer-left__link:hover {
  color: #FF9900;
  transition: color 0.2s ease;
}
.footer-left__link:focus {
  outline: 2px solid #FF9900;
}
.footer-left__link:active {
  color: #E1670E;
}

.footer-left__list {
  margin-bottom: 90px;
}

.footer-social {
  display: flex;
}
.footer-social a:not(:last-child) {
  margin-right: 25px;
}

.footer-soc__svg:hover path {
  fill: #FF9900;
}

.footer-soc__svg:focus path {
  fill: #666666;
  outline: 2px solid #FF9900;
}

.footer-soc__svg:active path {
  fill: #E1670E;
}

.footer-center__title {
  font-weight: 400;
  font-size: 48px;
  line-height: 1.22;
  color: #CACACA;
  margin-bottom: 25px;
}

.footer-center__form {
  display: flex;
  flex-direction: column;
}

.footer-center__input:not(:last-child) {
  margin-bottom: 25px;
}

.footer-center__input:nth-child(3) {
  display: flex;
  padding-bottom: 100px;
}

.footer-center__input {
  font-weight: 400;
  font-size: 16px;
  line-height: 2;
  color: #ffffff;
  background: #414141;
  border-radius: 16px;
  max-width: 570px;
  min-height: 60px;
  padding-left: 24px;
}
.footer-center__input:hover {
  background: #666666;
  transition: all 0.2s ease;
}
.footer-center__input:focus {
  background: #666666;
}
.footer-center__input:active {
  color: #FFFFFF;
  background: #666666;
}

.footer-center__input:nth-child(3) {
  min-height: 150px;
}

.footer-center__desc {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer-center__desc .footer-center__btn {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  color: #FFFFFF;
  background: #FF9900;
  border-radius: 16px;
  padding: 19px 52px;
}
.footer-center__desc .footer-center__btn:hover {
  background: #FFB84D;
  transition: all 0.2s ease;
}
.footer-center__desc .footer-center__btn:focus {
  background: #FFB84D;
}
.footer-center__desc .footer-center__btn:active {
  background: #E1670E;
}
.footer-center__desc p {
  font-weight: 400;
  font-size: 16px;
  line-height: 2;
  color: #CACACA;
}

.footer-right__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 2;
  color: #CACACA;
  max-width: 363px;
}

.checkbox-other {
  display: block;
  margin: 0 0 10px 0;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.checkbox {
  position: relative;
  padding-left: 30px;
}

.checkbox input[type=checkbox] {
  display: none;
}

.checkbox label:after {
  content: "";
  display: block;
  height: 14px;
  width: 14px;
  outline: 1px solid #939598;
  position: absolute;
  top: -7px;
  left: 10px;
  cursor: pointer;
  background: url("../img/footer/checkbox.svg") no-repeat center;
}

.checkbox input[type=checkbox]:checked + label:after {
  outline: 1px solid #939598;
  border: 2px solid #fff;
  width: 14px;
  height: 14px;
  background: url("../img/footer/footer_flag.svg") no-repeat center;
}

.footer-center__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 2;
  color: #CACACA;
}

@media (max-width: 1024px) {
  .swiper {
    width: 924px;
    height: 668px;
  }
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
  .project-box__title {
    font-size: 60px;
  }
  .project-box__subtitle {
    font-size: 20px;
  }
  .header__nav {
    display: none;
  }
  .footer__right {
    display: none;
  }
  .footer__container {
    display: flex;
    justify-content: space-between;
  }
  .about-box__left {
    margin: 0 0 32px 0;
  }
  .work-right__img {
    border-radius: 32px;
    max-width: 437px;
    min-height: 689px;
  }
  .work-left__text {
    max-width: 437px;
  }
  .work-left {
    min-height: 689px;
  }
  .ui-accordion-header-icon {
    display: none;
  }
  .hamburger-menu {
    display: block;
  }
  #menu__toggle {
    opacity: 0;
  }
  #menu__toggle:checked + .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked + .menu__btn > span::before {
    top: 0;
    transform: rotate(0deg);
  }
  #menu__toggle:checked + .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    left: 0 !important;
  }
  .menu__btn {
    position: absolute;
    top: 50px;
    left: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 103;
  }
  .menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
    transition-duration: 0.25s;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  .menu__box {
    justify-content: flex-end;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: -100%;
    width: 300px;
    height: 400px;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    transition-duration: 0.25s;
    z-index: 100;
    background: #FFFFFF;
    box-shadow: 0 5px 40px rgba(0, 0, 0, 0.1);
    border-radius: 0 0 32px 32px;
  }
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: "Roboto", sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: 0.25s;
  }
  .menu__item:hover {
    background-color: #CFD8DC;
  }
  .checkbox {
    padding-left: 30px;
  }
  .swiper-pagination {
    margin-bottom: 70px;
  }
}
